import React, { useState} from 'react';
const api = {
  key: '828708fa608203697302edbc3ba278d6',
  base: 'https://api.openweathermap.org/data/2.5/'
}

function App() {
  const [query, setQuery] = useState('');
  const [weather, setWeather] = useState({});
  
  const search = evt => {
    if(evt.key ==="Enter") {
      fetch(`${api.base}weather?q=${query}&units=metric&APPID=${api.key}`)
      .then(res => res.json())
      .then(result => {
        setWeather(result);
        setQuery(``);
        console.log(result)
      })
    }
  }



  const dateBuilder = (d) => {
    let months = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
    let days = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"];

    let day = days[d.getDay()];
    let date = d.getDate();
    let month = months[d.getMonth()];
    let year = d.getFullYear();

    return `${day} ${date} ${month} ${year}`
  }
  return (
    <div className={(typeof weather.main != "undefined") 
    ? ((weather.main.temp >16) ? 'app warm' : 'app') : 'app'}>
      <main>
       <select name="unit" className="unit">
          <option value="metric">Metric</option>
          <option value="imperial">Imperial</option>
      </select>
        <div className="search-box">
          <input 
          type="text"
          className="search-bar"
          placeholder="Search..."
          onChange={e=> setQuery(e.target.value)}
          value={query}
          onKeyPress={search}
          />
        </div>
        {(typeof weather.main != "undefined") ? (
        <div>
        <div className="location-box">
          <div className="location">{weather.name}, {weather.sys.country}</div>
          <div className="date">{dateBuilder(new Date())}</div>        
        </div>
        <div className="weather-box">
          <div className="temp">
            {Math.round(weather.main.temp)}째c
          </div>
          <div className="weather">{weather.weather[0].main}</div>
          <div className="second">
          <div className="feelslike">
            Feels like <br/>{Math.round(weather.main.feels_like)}째c
          </div>
          <div className="feelslike">
            Min temp <br/>{Math.round(weather.main.temp_min)}째c
          </div>
          <div className="feelslike">
            Max temp <br/>{Math.round(weather.main.temp_max)}째c
          </div>
          <div className="feelslike">
            Pressure <br/>{Math.round(weather.main.pressure)}
          </div>
          <div className="feelslike">
            Humidity <br/>{Math.round(weather.main.humidity)}
          </div>
          </div>
        </div>
        </div>
        ): ('')}
      </main>
    </div>
  );
}

export default App;

